@import "variable";

//// header/////
.header {
  &__logo {
    img {
      width: 5em;
    }
  }
  .traduire {
    position: absolute;
    right: 14px;
  }

  nav {
    padding: 0em 2em;
    background: url("../../Design/Assets/png/Navigation Bar 5.jpg") no-repeat
      center/cover;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
  }

  &__menu {
    a {
      margin-right: 3.4em;
      color: $text_color;
      font-weight: 700;
      font-size: 1.3em;
      overflow: auto;
    }
    .conseil {
      color: black;
    }
  }

  &__text {
    background: url("../../Design/Assets/png/En\ chiffres-24.png") no-repeat
      center/cover;
    text-align: center;
    line-height: 2;
    margin-top: 6.6em;
  }

  &__title {
    font-weight: 700;
    font-size: 1.3em;
  }

  &__parag {
    width: 40em;
    font-weight: 700;
    margin: 0px auto;
    font-size: 1.35em;
    color: $btn_color;
  }
}
